.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
  background: #e5e5e5;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden; }

@keyframes wheels {
  100% {
    transform: rotate(360deg); } }

.alwheels {
  animation: wheels 3s infinite linear; }

.preloader__back {
  position: absolute;
  display: block;
  width: 100%;
  left: 0;
  transition: opacity .3s ease-in-out;
  animation: preloader 20s linear;
  transform: translateX(7%);
  z-index: -100;
  opacity: 0; }

.preloader__image {
  position: absolute;
  display: block;
  width: 450px;
  height: 450px; }
  .preloader__image svg {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover; }

.preloader__forward {
  position: absolute;
  margin: 0;
  opacity: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -20%);
  transition: 0.3s cubic-bezier(0.77, 0.125, 0.265, 1.04); }

.preloader__background {
  position: absolute;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  z-index: -1;
  width: 330px;
  height: 220px; }
  .preloader__background::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    height: 100%;
    background: #fff;
    transition: 0.3s cubic-bezier(0.77, 0.125, 0.265, 1.04); }

@keyframes preloader {
  0% {
    transform: translateX(7%); }
  100% {
    transform: translateX(100%); } }

/* Preloading animation */
.preloader-animation .preloader__back {
  opacity: 0.15; }

.preloader-animation .preloader__forward {
  opacity: 1;
  transform: translate(-50%, -50%); }

.preloader-animation .preloader__background::before {
  width: 330px;
  transition-delay: .3s;
  transition: width .3s ease-in-out; }

.preloader-hidden .preloader__back {
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.77, 0.125, 0.265, 1.04), transform 0.3s cubic-bezier(0.77, 0.125, 0.265, 1.04); }

.preloader-hidden .preloader__background::before {
  width: 0; }

.preloader-hidden .preloader__forward {
  opacity: 0;
  transition-delay: .3s;
  transition: .3s ease-in-out; }

.preloader-hidden.preloader-none {
  opacity: 0;
  visibility: hidden;
  transition: .3s ease-in-out; }

