.cards__list-item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(100% - 20px);
  margin-bottom: 20px;
  padding: 20px 20px 40px;
  background: #fff;
  box-shadow: 5px 5px 5px 0 rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  @media screen and (min-width: 1200px) {
